body{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.calculadora{
     background-color: rgba(128, 128, 128, 0.774);
     width: 457px;
     height: 600px;
     border-top-left-radius: 15px;
     display: flex;
     flex-direction: column
;
     
}
#resultado{
  background-color: green;
  height: 90px;
  font-size: 45px;
  text-align: right;
  border-top-left-radius: 15px;
  font-weight: bold;
  color: white;
  
}

#operacoes{
  display:grid;
  grid-template: 112px 112px 112px 112px/112px 112px 112px 112px;
  background-color: hotpink;
  gap: 3px;
}

.special{
  grid-column: 1/3;
  background-color: red;
  font-size: 30px;

}

.numero{
  background-color: indigo;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  color: white;
  font-weight: bold;
  font-family: 'Courier New', Courier, monospace;
  cursor: pointer;
}

.operacoes-btn{
   background-color:blue;
   display: flex;
   align-items: center;
   justify-content: center;
   font-size: 30px;
   color: white;
   font-weight: bold;
   cursor: pointer;
}

#resultado-btn{
   display: grid;
   grid-template-columns: 1fr 1fr;
   height: 55px;
   cursor: pointer;
}

#reset{
  background-color: tomato;
  display: grid;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-weight: bold;
  color: white;

  cursor: pointer;

}
#igual{
  background-color: yellowgreen;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-weight: bold;
  color: white;
}